import React, {Component} from "react";

import InputWithFloatingLabel from "../../magnum2/components/InputWithFloatingLabel";
import TransactionProcessType from "../../core/constants/TransactionProcessTypeEnum";
import ShowSectionName from "../../core/constants/ShowSectionNameEnum";

class MsisdnSelectionSectionComponent extends Component {
    constructor(props) {
        super(props);
        this.state={msisdn:""};
    }

    inputLabel() {
        if(this.props.isFix && this.props.enablePeselAuth){
            return this.props.descriptions.inputLabelWithPesel || "Podaj swÃ³j nr telefonu, login lub PESEL"
        }
        if (this.props.showModalForProcess) {
            return this.props.descriptions.inputLabelPhoneOnly || "Podaj numer telefonu"
        } else {
            return this.props.descriptions.inputLabel
        }
    }

    toHTML(content) {
        return {__html: content};
    }

    componentWillUnmount() {
        OPL.UI.stopModules(document.getElementById("smsVerificationMsisdnSection"));
        console.log("unmount sms")
    }

    ommitAccountAuth() {
        this.props.ommitAccountAuth();
    }

    validatePesel(pesel){

            var reg = /^[0-9]{11}$/;
            if (reg.test(pesel) === false) {
                return false;
            }
            else {
                var digits = ("" + pesel).split("");
                if ((parseInt(pesel.substring(4, 6)) > 31) || (parseInt(pesel.substring(2, 4) % 20) > 12)) {
                    return false;
                }

                var checksum = (1 * parseInt(digits[0]) + 3 * parseInt(digits[1]) + 7 * parseInt(digits[2]) + 9 * parseInt(digits[3]) + 1 * parseInt(digits[4]) + 3 * parseInt(digits[5]) + 7 * parseInt(digits[6]) + 9 * parseInt(digits[7]) + 1 * parseInt(digits[8]) + 3 * parseInt(digits[9])) % 10;
                if (checksum === 0) {
                    checksum = 10;
                }
                checksum = 10 - checksum;

                return (parseInt(digits[10]) === checksum);
            }

    }
    validateMsisdn() {
        console.log("VALIDATE MSISDN FIELD");
        let msisdn = this.getMsisdnInputVal();
        if(!msisdn)
                return true;
        console.log(this.getMsisdnInputVal());
        if ((msisdn.length === 9 && jQuery.isNumeric(msisdn)))
            return true;

        if(!this.props.showModalForProcess) {
            if (this.props.enablePeselAuth && (msisdn.length === 11 && jQuery.isNumeric(msisdn)))
                return this.validatePesel(msisdn);
            else
                return !msisdn || msisdn && /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(msisdn);
        }
    }

    getMsisdnInputVal() {
        return this.state&&this.state.msisdn&&this.state.msisdn.replace(/-/g, "");
    }

    getPropsForInput(name) {
        return {
            name,
            value: this.props[name] || "",
            errors: this.props.errors && this.props.errors[name],
            valid: this._validate.bind(this)(name),
            validateEmpty: this.props.isExistingCustomer,
            disabled: this.props.readOnly,
            onChange: this.props.changeCustomerContactFormField,
        };
    }

    onSubmit(event) {
        event.preventDefault();
        console.log("OnSubmit login token");
        if (!this.validateMsisdn()) {
            if (this.props.isFix && this.props.enablePeselAuth)
                this.props.showError("Wpisz poprawny numer, login lub pesel!");
            else if(this.props.showModalForProcess)
                this.props.showError("Wpisz poprawny numer!");
            else
                this.props.showError("Wpisz poprawny numer lub login!");
            this.refInput.focus();
            return;
        }
        let msisdn = this.getMsisdnInputVal();
        this.props.clearMessage();
        this.props.setMSISDN(msisdn);
        console.log("getting msisdn from field= " + msisdn);
        if (this.props.msisdnVerificationIsNotRequired()) {
            console.log("msisdn holder verification is not required");
            this.props.showMessage("Trwa weryfikacja numeru.");
            this.props.pushVerifiedMsisdn(msisdn, this.props.processType, this.props.propositionId, !this.props.accountSelectionInNotRequired() && "ACCOUNT", this.props.selectedDeviceId);
        } else {
            this.props.setShowSection(ShowSectionName.CAAP);
            this.props.showMessage("Trwa weryfikacja numeru.");
            this.props.setMsisdn(msisdn, this.props.processType);
        }
    }

    onMsisdnFieldChange(valiObj) {
        this.setState({msisdn: valiObj.value});
    }

    isTanto() {
        return TransactionProcessType.isTanto(this.props.processType);
    }


    render() {
        let processType = this.props.processType;
        let text = this.props.descriptions[["mainLabel", processType].filter(Boolean).join(".")];
        let accountIndentify = this.props.showModalForAccountIdentify && !this.props.showModalForProcess;
        if (accountIndentify) {
            text = this.props.descriptions[["mainLabel", "accountIdentify", processType].filter(Boolean).join(".")];
        }
        if (this.props.isCustomerDataStep && this.props.isAuthNeeded) {
            text = this.props.descriptions["mainLabel.customerExists"];
        }
        let showOmmit=!!(this.props.clientContext && this.props.showModalForAccountIdentify && !this.props.showModalForProcess && !this.isTanto());
        return (
            <div id="smsVerificationMsisdnSection">
                <p>
                    <span id="opl-sms-modal-main-content" aria-atomic="true" className="u-spacing-right-s"
                          aria-live="assertive" dangerouslySetInnerHTML={this.toHTML(text)}/>
                </p>
                <MessageContainer
                    content={this.toHTML(getDesc('infoMessage', this.props.descriptions, this.props.processType))}/>
                <form id="msisdnForm" onSubmit={this.onSubmit.bind(this)}>
                    <div className="l-row u-spacing-top-m">
                        <div className="l-col l-col-small-12 l-col-medium-8 l-col-8 u-small-padding">
                            <InputWithFloatingLabel
                                id="msisdn"
                                value={this.state.msisdn}
                                required={true}
                                onChange={this.onMsisdnFieldChange.bind(this)}

                                placeholder={this.inputLabel()}

                                focusOnMount={true}

                                showValidation={true}
                                showErrors={true}
                                valid={this.validateMsisdn()}
                                errors={!(this.validateMsisdn())}

                                overrideClass={"o-floating-label opl-floating-label-line opl-floating-label-line--white opl-autocomplete u-w-100"}
                            />

                        </div>
                        <div className="l-col l-col-small-12 l-col-medium-4 l-col-4 ">
                            <button id="sms" type="submit"
                                    className="o-btn opl-btn opl-btn--primary u-w-100">{this.props.descriptions.next}</button>
                        </div>
                    </div>
                </form>


                <div className="l-row u-spacing-top-m">
                    {(this.props.showModalForAccountIdentify && !this.props.showModalForProcess) &&
                    <div className={"l-col  l-col-small-12 u-text-left u-left "+ (showOmmit?" l-col-6":" l-col-12")}>
                        <span dangerouslySetInnerHTML={{__html:this.props.descriptions.registerLabel}}/>
                    </div>}
                    {showOmmit &&
                    <div className="l-col l-col-6  l-col-small-12  u-text-right u-right">
                        <a id="ommit" href="#" onClick={() => this.ommitAccountAuth()} className="">
                            {this.props.descriptions.ommit || "Dodaj do koszyka bez logowania"}</a>
                    </div>}
                </div>

            </div>
        )
    }
}

const MessageContainer = props => {
    return (
        <div className="opl-msg opl-msg--box opl-msg--info u_margin-l u-margin-top-l">
            <div className="o-icon-list">
                <div className="o-icon-list__item">
                    <div className="o-icon-list__icon u-vertical-middle">
                        <div aria-hidden="true" className="g-icon g-icon--info g-icon--before g-icon--s"/>
                    </div>
                    <div className="o-icon-list__text u-vertical-middle">
                        <div dangerouslySetInnerHTML={props.content}/>
                    </div>
                </div>
            </div>
        </div>
    )
};

const getDesc = (name, values, filter) => {
    let desc = values[[name, filter].filter(Boolean).join(".")];
    return desc || values[name] || "This is default message for smsVerificationComponent based on \"infoMessage\" CMSDescriptionItem.";
};

export default MsisdnSelectionSectionComponent;